
.projects-container .project-list{-ms-flex-align:start;align-items:flex-start;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:distribute;justify-content:space-around
}
.projects-container .project-list .project-list-item{display:-ms-flexbox;display:flex;margin-bottom:56px
}
@media screen and (max-width: 650px){
.projects-container .project-list .project-list-item{margin-bottom:56px
}
}
.projects-container .project-list li:nth-of-type(2n)>div{-ms-flex-direction:row-reverse;flex-direction:row-reverse
}
.projects-container .project-list li:nth-of-type(1n+2){border-top:2px solid #bcbcbc
}
@media screen and (max-width: 650px){
.projects-container .project-list li:nth-of-type(1n+2){border:none
}
}
.projects-container .project{border-radius:5px;display:-ms-flexbox;display:flex;height:200px;-ms-flex-pack:distribute;justify-content:space-around;margin-top:32px;position:relative;width:100%
}
@media screen and (max-width: 500px){
.projects-container .project{-ms-flex-wrap:wrap;flex-wrap:wrap;text-align:center
}
}
.projects-container .project a{width:50%
}
@media screen and (max-width: 650px){
.projects-container .project a{width:inherit
}
}
.projects-container .project .project-img{background-image:url(../assets/BrewRank.png);background-size:cover;border:1px solid #bcbcbc;border-radius:5px;height:200px;width:100%
}
.projects-container .project .project-img:hover{box-shadow:3px 3px 3px #afafaf
}
@media screen and (max-width: 500px){
.projects-container .project .project-img{height:120px;margin:0 auto;width:250px
}
}
.projects-container .project .project-info{width:40%
}
@media screen and (max-width: 500px){
.projects-container .project .project-info{width:90%
}
}
.projects-container .project .project-info .project-title{margin-bottom:0
}



