
body, html {
  color: #bcbcbc;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app-container {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  overflow-x: hidden;
}
.section-head {
  margin: 5% 0;
}
ul, li{
  padding: 0;
}
li {
  list-style: none;
}
@media screen and (max-width: 500px) {
.section-head {
    text-align: center;
}
}



