
.nav-container{background-color:#313233;height:100%;padding-bottom:10px;position:fixed;top:0;text-align:center;width:24%;z-index:1
}
.nav-container a,.nav-container a:visited{color:#bcbcbc;list-style:none;text-decoration:none
}
.nav-container a:hover{border-bottom:2px solid #bcbcbc
}
.nav-container a:active{border-bottom:2px solid #42b4d6;color:#42b4d6
}
.nav-container nav{height:100%
}
.nav-container h2{font-size:2.5em;font-weight:bold;margin:0;padding-top:48px
}
.nav-container #callout{color:#42b4d6
}
@media screen and (max-width: 1100px){
.nav-container{-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;height:8%;width:100%
}
.nav-container nav{-ms-align-items:center;-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;width:50%
}
.nav-container h2{margin-right:auto;padding:10px 0 10px 10px
}
}
@media screen and (max-width: 800px){
.nav-container{-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;height:10%;width:100%
}
}
@media screen and (max-width: 620px){
.nav-container{flex-wrap:wrap;-webkit-flex-wrap:wrap;-moz-flex-wrap:wrap;-ms-flex-wrap:wrap;-o-flex-wrap:wrap;height:15%;-ms-flex-pack:center;justify-content:center
}
.nav-container nav{height:inherit
}
.nav-container h2{margin-right:0;padding:10px 0 0 0
}
}
@media screen and (orientation: landscape) and (max-width: 1000px){
.nav-container{padding:10px 0
}
}



